// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
/* eslint-disable */
import Vue from 'vue'
import App from './App'
import router from './router'
import {langdetector} from '@/langdetector';



//setIdiomaInicial 
localStorage.setItem('language', langdetector());

//VUEX 
import Vuex from 'vuex'
Vue.use(Vuex)


const store = new Vuex.Store({
  state :{
    language: localStorage.getItem('language'),
    server : 'http://52.166.203.221/',
    textos : messages[localStorage.getItem('language')],
    likes : JSON.parse(localStorage.getItem('likes')) || new Array(),
    hints : JSON.parse(localStorage.getItem('hints')) || new Array()
  },
  actions :{
    changeLanguage : ({commit}, actualLang) => {
      return new Promise((resolve, reject) => {
        let nextLang = (actualLang == "es") ? "en" : "es";
        commit('setLanguage', nextLang);
        resolve(nextLang);
      });
    },
    sendLike : ({commit}, slug) => {
      return new Promise((resolve, reject) => {
        axios.get(`like/${slug}`).then(response => {
          resolve(response.data.likes);
          commit('addLike', slug)
        })
        .catch(function (error) {
          reject(false);
        });
      });    
    },
    sendHint : ({commit}, slug) => {
      return new Promise((resolve, reject) => {
        axios.get(`hint/${slug}`).then(response => {
          resolve(response.data.hints);
          commit('addHint', slug)
        })
        .catch(function (error) {
          reject(false);
        });
      });    
    }
  },
  getters : {
    getLanguage : state => state.language,
    getServer : state => state.server,
    getTexts : state => state.textos,
    getlike(state) {
      return (slug) => {
        return state.likes.some(l => l == slug)
      }
    },
    gethint(state) {
      return (slug) => {
        return state.hints.some(l => l == slug)
      }
    }
  },
  mutations : {
    setLanguage (state, lang){
      state.language = lang;
      localStorage.setItem('language', lang);
    },
    addLike(state, slug){
      state.likes.push(slug);
      window.localStorage.setItem('likes',JSON.stringify(state.likes));
    },
    addHint(state, slug){
      state.hints.push(slug);
      window.localStorage.setItem('hints',JSON.stringify(state.hints));
    }
  }
});

//.VUEX 


//AXIOS
import axios from 'axios'
import VueAxios from 'vue-axios'
axios.defaults.baseURL = store.state.server;
//.AXIOS

//VEE-VALIDATE
import VeeValidate, {Validator} from 'vee-validate';
import validatorEs from '@/validator/es';
Validator.localize('es', validatorEs);
Vue.use(VeeValidate);
//.VEE-VALIDATE


//IDIOMAS
import VueI18n from 'vue-i18n'
import messages from './translations';

Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: store.state.language, 
  messages,
});
//.IDIOMAS


//SCROLLTO

var VueScrollTo = require('vue-scrollto'); 
Vue.use(VueScrollTo,{
  offset: -120,
})


Vue.config.productionTip = false

new Vue({
	el: '#app',
	router,
	i18n,
	store,
  VeeValidate,
  VueScrollTo,
  components: { App },
  template: '<App/>'
})



