          <h2 class="title title-52" v-html="$t('blog.intro.titulo')"></h2>
          <div class="flex">
            <h5>{{$t('blog.intro.subtitulo')}}</h5>
            <img class="logo" src="/static/img/logo.svg"/>
          </div>
        </div>
      </div>
      <div class="col-40 flex column nowrap justify-center align-center animated fadeInLeft">
        <!-- <img src="/static/img/blog/bkg-blog.jpg"/> -->
      </div>
    </section>
  
</template>

<script lang="js">
  export default  {
    name: 'blog',
    props: [],
    mounted() {

    },
    data() {
      return {

      }
    },
    methods: {

    },
    computed: {

    }
}
</script>

<style scoped>
@media only screen and (max-width: 800px) {
 .intro .col-50, .intro-xs .col-50{
  width: 50%;
 }
 .blog .intro .col-40{
  width: 50%;
 }
 .blog .intro .col-50 .title-52{
  width: 145%;
 }
}

@media only screen and (max-width: 630px) {
 .intro .col-50, .intro-xs .col-50{
  width: 100%;
 }
 .blog .intro .col-40{
  width: 100%;
 }
 .blog .intro .col-50 .title-52{
  width: 100%;
 }
 .blog .intro .col-50 .title-52{
  padding: 20px;
 }
 
}
</style>



