<template lang="html">

  <div class="blog--view blog">

    <intro></intro>

    <section class="cat-blog">

      <div class="rrss-blog  container flex wrap justify-between">
        <div class="col-50">

        </div>
        <div class="col-50 flex column justify-center align-stretch ">
          <ul class=" flex justify-center">
            <li><a class="fa fa-twitter" href="https://twitter.com/ajusa_spain?lang=es" target="_blank"></a></li>
            <li><a class="fa fa-facebook" href="https://www.facebook.com/AjusaSpain/" target="_blank"></a></li>
            <li><a class="fa fa-instagram" href="https://www.instagram.com/ajusaspain/" target="_blank"></a></li>
            <li><a class="fa fa-youtube" href="https://www.youtube.com/channel/UCnoYVeFbsPiOVOcf80tW6Bg" target="_blank"></a></li>
          </ul>
        </div>
      </div>

      <div class="pad-sec">
        <div class="container flex wrap justify-between">
          <div class="col-20">
            <router-link :to="$t('enlaces.blog_categoria')+$t('enlaces.ferias')" class="img" data-value="cat-blog1"></router-link>
            <h4 class="tit-cat">{{$t('blog.categorias_nombres["ferias"]')}}</h4>
          </div>
          <div class="col-20">
            <router-link :to="$t('enlaces.blog_categoria')+$t('enlaces.productos')" class="img" data-value="cat-blog2"></router-link>
            <h4  class="tit-cat">{{$t('blog.categorias_nombres["productos"]')}}</h4>
          </div>
          <div class="col-20">
            <router-link :to="$t('enlaces.blog_categoria')+$t('enlaces.nuestro_sector')" class="img" data-value="cat-blog3"></router-link>
            <h4  class="tit-cat">{{$t('blog.categorias_nombres["nuestro-sector"]')}}</h4>
          </div>
          <div class="col-20">
            <router-link :to="$t('enlaces.blog_categoria')+$t('enlaces.compromiso_social')" class="img" data-value="cat-blog4"></router-link>
            <h4  class="tit-cat">{{$t('blog.categorias_nombres["compromiso-social"]')}}</h4>
          </div>
        </div>
      </div>
    </section>


    <template v-if="!!destacados">

      <destacado-el v-for="(e, key) in destacados" :key="key" :data="e"></destacado-el>

    </template>

    <section class="destacados pad-sec">
      <div class="container">
        <h2 class="title title-36">{{$t('blog.ultimas')}}</h2>
        <div class="flex wrap justify-around">
         <template v-if="!!noticias">
          <noticia-el v-for="(e, key) in noticias" :data="e" :key="key"></noticia-el>
        </template>
      </div>
    </div>
  </section>

</div>

</template>

<script lang="js">

  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'
  import { mapGetters } from 'vuex'

  Vue.use(VueAxios, axios);

  import Noticia_el from '@/components/parts/noticia-el';
  import Intro from '@/components/view-parts/intros/blog'

  import Destacado_el from '@/components/parts/destacado-blog-el';



  export default  {
    name: 'blog',
    props: [],
    mounted() {
     axios.get(`blog?lang=${this.lang}`)
     .then(response => {
      this.noticias = response.data.noticias;
    })
     .catch(error => console.log(error));
   },
   data() {
    return {
      noticias : ''
    }
  },
  computed: {
    ...mapGetters({
      lang: 'getLanguage',
    }),
    destacados : function (){
      let posts;
      if(!!this.noticias){
        posts = this.noticias.filter(e => e.destacado);
      }
      return posts;
    }
  },
  components: {
    'intro' : Intro,
    'noticia-el' : Noticia_el,
    'destacado-el' : Destacado_el
  }
}
</script>

<style scoped>
.blog {

}
</style>



