<template lang="html">

  <div class="home--view home">

    <intro></intro>  

      <section class="excelencia">
        <div class="barco pad-sec">
          <div class="container">
            <div class="container-text waypoints" data="fadeInUp">
              <h2 class="title title-36" v-html="$t('home.excelencia.intro')"></h2>
              <p v-html="$t('home.excelencia.texto')"></p>
            </div>
          </div>
        </div>
      </section>


      <section class="calidad">

        <article class="calidad-1">
          <div class="container">
            <div>
              <h2 class="title">
                <span class="first bold title-114 waypoints" v-html="$t('home.calidad.titulo.first')"></span>
                <span class="second waypoints" v-html="$t('home.calidad.titulo.second')"></span>
                <span class="third waypoints" v-html="$t('home.calidad.titulo.third')"></span>
                <span class="fourth waypoints" v-html="$t('home.calidad.titulo.fourth')"></span>
              </h2>
            </div>
            <div class="down flex wrap justify-center">
              <img alt="flecha" src="/static/img/flecha_blanca.svg" >
            </div>
            <div class="flex row-reserve justify-end">
              <p>{{$t('home.calidad.texto')}}</p>
            </div>
          </div>
          <div class="scrollme pieza-1 waypoints" data="fadeInUp" >
            <img class="animateme" src="/static/img/home/pieza-1.png">
          </div>
        </article>


        <article class="calidad-2">
          <div class="flex wrap justify-center">
            <div class="superponer flex column justify-start waypoints" data="fadeInLeft">
              <p class="margin-p" v-html="$t('home.calidad_2.texto_1')"></p>
              <div class="scrollme pieza-2 waypoints" data="fadeInUp" >
                <img class="animateme" src="/static/img/home/pieza-2.png">
              </div>
            </div>
            <div class="blue-block pad-art flex column justify-center waypoints" data="fadeInRight">
              <p v-html="$t('home.calidad_2.texto_2')"></p>
              <div class="scrollme pieza-3 waypoints" data="fadeInUp" >
                <img class="animateme" src="/static/img/home/pieza-3.png">
              </div>
            </div>
          </div>
        </article>

        <article class="calidad-3 flex row-reserve wrap justify-start align-strech">
          <div class="bkg-metal col-50 pad-art flex column justify-center align-end">
            <h4 class="title title-36" v-html="$t('home.calidad_3.titulo')"></h4>
          </div>
          <div class="col-50 pad-art flex column justify-end">
            <p v-html="$t('home.calidad_3.texto')"></p>
          </div>
        </article>

      </section>

      <section class="ver-productos pad-sec">
        <router-link :to="$t('enlaces.productos')"></router-link>
        <div class="container waypoints" data="fadeInUp">
          <h6 class="title title-26" v-html="$t('home.productos.titulo')"></h6>
          <div class="mas-light"></div>
        </div>
      </section>
    
      <destacados></destacados>

    </div>

  </template>

  <script lang="js">
    
    import Intro from '@/components/view-parts/intros/home'

    import Destacados from '@/components/view-parts/home/destacados'

    export default  {
      name: 'home',
      props: [],
      mounted() {

      },
      data() {
        return {

        }
      },
      methods: {

      },
      components: {
        'intro' : Intro,
        'destacados' : Destacados
      }
    }
  </script>

  <style scoped>
  .index {

  }
</style>



