<template lang="html">

  <section class="intro-xs" :data-bkg="where">
    <div class="col-50 flex column nowrap justify-center align-center animated fadeInLeft">
      <div class="container-blog">
        <h2 class="title title-26" v-html="$t('blog.intro.titulo')"></h2>
        <div class="flex">
          <h5>{{$t('blog.intro.subtitulo')}}</h5>
          <img class="logo" src="/static/img/logo.svg"/>
        </div>
      </div>

      <div class="container-text">
        <h2 class="title title-52">{{data}}</h2>
      </div>
    </div>
  </section>

</template>

<script lang="js">
  export default  {
    name: 'ficha-blog',
    props: ['data', 'categoria'],
    mounted() {

    },
    data() {
      return {

      }
    },
    methods: {

    },
    computed: {
      where : function (){

        let where;


        if(!!!this.data){

          if(!!this.categoria){
            where = this.categoria;
          }else{
            where = this.$route.params.id;
          }
        }

        return where;

      }
    }
  }
</script>

<style scoped>
[data-bkg="nuestro-sector"]{
  background: url(/static/img/blog/cat-blog3.jpg) no-repeat center right/50%;
}

[data-bkg="ferias"]{
  background: url(/static/img/blog/blog-ferias.jpg) no-repeat center right/50%;
}

[data-bkg="compromiso-social"]{
  background: url(/static/img/blog/cat-blog4.jpg) no-repeat center right/50%;
}

[data-bkg="productos"]{
  background: url(/static/img/blog/blog-productos.jpg) no-repeat center right/50%;
}

.title-26{
  background: #fff;
  color: #9bb3c9;
  padding: 4px 20px;
  position: relative;
  bottom: inherit;
}

@media only screen and (max-width: 800px) {
  [data-bkg="nuestro-sector"]{
   background-position-x:140%; 
 }

 [data-bkg="ferias"]{
   background-position-x:140%; 
 }
 
 [data-bkg="compromiso-social"]{
   background-position-x:140%; 
 }

 [data-bkg="productos"]{
   background-position-x:140%; 
 }
}
@media only screen and (max-width: 600px) {
  [data-bkg="nuestro-sector"],[data-bkg="ferias"],
  [data-bkg="compromiso-social"],[data-bkg="productos"]{
    background-size: cover; 
    background-position: center center;
  }
  
  

}
</style>



