        <h3 class="title title-36">POLTICA DE COOKIES</h3>
        <p class="bold">Informacin en cumplimiento de la normativa de proteccin de datos personales</p>
        <p class="bold">Qu son las cookies y para qu las usamos?</p>
        <p>Una cookie o galleta informtica es un pequeo archivo de informacin que se guarda en su navegador cada vez que visita nuestra pgina web. </p>
        <p>La utilidad de las cookies es guardar el historial de su actividad en nuestra pgina web, de manera que, cuando la visite nuevamente, sta pueda identificarle y configurar el contenido de la misma en base a sus hbitos de navegacin, identidad y preferencias.</p>
        <p>Una cookie es inofensiva, no contiene cdigo malicioso o malintencionado (ej. virus, troyanos, gusanos, etc.) que pueda daar su terminal (ordenador, smartphone, tableta, etc.), pero s tiene cierto impacto sobre su derecho a la proteccin de sus datos, pues recoge determinada informacin concerniente a su persona (hbitos de navegacin, identidad, preferencias, etc.). </p>

        <p class="bold">Qu informacin guarda una cookie?</p>
        <p> Las cookies no suelen recoger categoras especiales de datos personales (datos sensibles). Los datos que guardan son de carcter tcnico, preferencias personales, personalizacin de contenidos, etc.</p>

        <p class="bold">Qu tipo de cookies existen?</p>
        <p>Con carcter general, existen cinco tipos de cookies:</p>
        <ul>
          <li> <p class="bold">Cookies tcnicas:</p> 
            <p>Son las cookies ms bsicas. Permiten al usuario la navegacin a travs de una pgina web, plataforma o aplicacin y la utilizacin de las diferentes opciones o servicios que en ella existan como, por ejemplo, controlar el trfico y la comunicacin de datos, identificar la sesin, acceder a partes de acceso restringido, recordar los elementos que integran un pedido, realizar el proceso de compra de un pedido, realizar la solicitud de inscripcin o participacin en un evento, utilizar elementos de seguridad durante la navegacin, almacenar contenidos para la difusin de videos o sonido o compartir contenidos a travs de redes sociales.</p>
          </li>

          <li> <p class="bold"> Cookies de personalizacin:</p> 
            <p>Son aqullas que permiten al usuario acceder al servicio con algunas caractersticas de carcter general predefinidas en funcin de una serie de criterios en el terminal del usuario como por ejemplo serian el idioma, el tipo de navegador a travs del cual accede al servicio, la configuracin regional desde donde accede al servicio, etc.</p>
          </li>

          <li> <p class="bold"> Cookies de anlisis:</p>
            <p>Son aqullas que permiten al responsable de las mismas, el seguimiento y anlisis del comportamiento de los usuarios de los sitios web a los que estn vinculadas. La informacin recogida mediante este tipo de cookies se utiliza en la medicin de la actividad de los sitios web, aplicacin o plataforma y para la elaboracin de perfiles de navegacin de los usuarios de dichos sitios, aplicaciones y plataformas, con el fin de introducir mejoras en funcin del anlisis de los datos de uso que hacen los usuarios del servicio.</p>
          </li>

          <li> <p class="bold"> Cookies publicitarias:</p>
            <p>Son aqullas que permiten la gestin, de la forma ms eficaz posible, de los espacios publicitarios que, en su caso, el responsable haya incluido en una pgina web, aplicacin o plataforma desde la que presta el servicio solicitado en base a criterios como el contenido editado o la frecuencia en la que se muestran los anuncios.</p></li>

            <li> <p class="bold">Cookies de publicidad comportamental:</p> 
              <p>Son aqullas que permiten la gestin, de la forma ms eficaz posible, de los espacios publicitarios que, en su caso, el responsable haya incluido en una pgina web, aplicacin o plataforma desde la que presta el servicio solicitado. Estas cookies almacenan informacin del comportamiento de los usuarios obtenida a travs de la observacin continuada de sus hbitos de navegacin, lo que permite desarrollar un perfil especfico para mostrar publicidad en funcin del mismo.</p>
            </li>

          </ul>

          <p class="bold">Qu son las cookies propias y las de terceros?</p>


          <ul>
            <li>
             <p class="bold">Cookies de propias: </p>
             <p>Las cookies propias son aquellas que se generan y gestionan por el propio responsable que presta el servicio solicitado por el usuario.</p>
           </li>
           <li><p class="bold">Cookies de terceros: </p>
            <p>Son aquellas que se generan por otras entidades distintas al propio responsable (servicios o proveedores externos, como por ejemplo Google).</p>
          </li>
        </ul>



        <p class="bold">Qu tipo de cookies guarda nuestra pgina web?</p>
        <p>A continuacin, procedemos a relacionar el tipo de cookies que guarda nuestra pgina web y la finalidad de las mismas:</p>

        <ul >
          <li class="no-disc">
            <p class="bold">- Tipo de cookie</p>
            <p>
              Cookies de terceros, de anlisis: Google Analytics 
            </p>
          </li>
          <li class="no-disc">
            <p class="bold">- Finalidad</p>
            <p>Seguimiento y anlisis del comportamiento de los usuarios en la pgina web. La informacin recogida mediante este tipo de cookies se utiliza en la medicin de la actividad de los sitios web, para la elaboracin de perfiles de navegacin de los usuarios de dichos sitios, aplicaciones y plataformas, con el fin de introducir mejoras en funcin del anlisis de los datos de uso que hacen los usuarios del servicio.
            </p>
          </li>
          <li class="no-disc">
            <p class="bold">
              - Cundo y cmo se guarda
            </p>
            <p>
              La cookie registra la actividad del usuario en el tiempo real de su navegacin y los datos se almacenan en la aplicacin Google Analytics. 
            </p>
          </li>

        </ul>
        <p>La pgina web de AJUSA tambin trabaja con la aplicacin Local Storage. </p>
        <p class="bold">Qu puedo hacer con las cookies?</p>
        <p>Las cookies pueden ser borradas, aceptadas o bloqueadas, segn desee, para esto slo debe configurar convenientemente el navegador web.</p>
        <p>En cualquier momento, puede impedir la instalacin de cookies (bloqueo) en su equipo mediante la opcin correspondiente de su navegador, pero en dicho caso no podremos asegurarle el correcto funcionamiento de las distintas funcionalidades de nuestra pgina web.</p>
        <p>A continuacin, le facilitamos los enlaces para la gestin y bloqueo de cookies dependiendo del navegador que utilice:</p>
        <p >Internet Explorer: <u><a href="http://windows.microsoft.com/es-xl/internet-explorer/delete-manage-cookies#ie=ie-10">http://windows.microsoft.com/es-xl/internet-explorer/delete-manage-cookies#ie=ie-10</a></u></p>
        <p > FireFox: <u><a href="http://support.mozilla.org/es/kb/habilitar-y-deshabilitar-cookies-que-los-sitios-we">http://support.mozilla.org/es/kb/habilitar-y-deshabilitar-cookies-que-los-sitios-we</a></u></p>
        <p >Chrome: <u><a href="https://support.google.com/chrome/answer/95647?hl=es">https://support.google.com/chrome/answer/95647?hl=es</a></u></p>
        <p >Safari: <u><a href="https://www.apple.com/legal/privacy/es/cookies/">https://www.apple.com/legal/privacy/es/cookies/</a></u></p>
        Tambin puede borrar las cookies que tenga guardadas en su navegador acudiendo a las opciones de configuracin del mismo.


