<template lang="html">

 <div class="language">
  <a @click="requestLangChange">
    <img class="bandera" :src="`/static/img/change-to-${this.getLanguage}.png`">
  </a>
</div>

</template>

<script lang="js">

  import {mapGetters,mapActions} from 'vuex';

  import texts from '@/translations'

  import router from '@/router'

  export default  {
    name: 'idioma',
    props: [],
    data(){
      return {
        oldLang : ''
      }
    },
    methods: {
      requestLangChange : function(){

        this.oldLang = this.getLanguage;

        this.changeLanguage(this.getLanguage)
        .then(r => {
          this.$i18n.locale = r;
          this.changePath(); 
        }).catch(e =>{
          console.log(e);
        })
      },
      changePath : function (){

        let enlaces = texts[this.oldLang].enlaces;
        let path;
        let newPath;

        for(let e in enlaces){
          (enlaces[e] == this.$route.path) ? path = e : '';
        }

        if(!!path){
          newPath = texts[this.getLanguage].enlaces[path];
          router.push(newPath); 
        }
      },
      ...mapActions(['changeLanguage']),
    },
    computed: {
     ...mapGetters(['getServer', 'getLanguage', 'getTexts'])
   }
 }
</script>

<style scoped>
.language {
  text-align: right;
} 
.bandera{
  max-width: 40px;
  width: 100%;
}

@media screen and (max-width: 1024px){
  .language{
    text-align: center;
  }
}
@media only screen and (max-width: 500px) {
  .bandera{
    width: 30px;
  }
}

</style>



