import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/views/home'
import Productos from '@/components/views/productos'
import Catalogo from '@/components/views/catalogo'
import Area_tecnica from '@/components/views/area-tecnica'
import Ajusa from '@/components/views/ajusa'
import Actualidad from '@/components/views/actualidad'
import Contacto from '@/components/views/contacto'
import Blog from '@/components/views/blog'
import Ficha_blog from '@/components/views/ficha-blog'
import Categoria_blog from '@/components/views/categoria-blog'
import SalaPrensa from '@/components/views/sala-prensa'
import Historia from '@/components/views/historia'
import PoliticaPrivacidad from '@/components/views/politica-privacidad'
import PoliticaCookies from '@/components/views/politica-cookies'
import DatosSeguros from '@/components/views/datos-seguros'
import MapaWeb from '@/components/views/mapa-web'
import TrabajaConNosotros from '@/components/views/trabaja-con-nosotros'
import AvisoLegal from '@/components/views/aviso-legal'
import Error404 from '@/components/views/error-404'


Vue.use(Router);

import messages from '@/translations';

let lang = localStorage.getItem('language');
(!!!lang) ? lang = "es" : '';

let paths_en = messages.en.enlaces;
let paths_es = messages.es.enlaces;

const router = new Router({
	mode : 'history',
	routes: [
	{
		path: '/',
		name: 'home',
		component: Home,
		meta :{
			title : 'Ajusa',
			description: 'Ajusa ofrece una ampla gama de componentes para el motor y de piezas de recambio para vehculos. Fabricante de juntas de culata, tornillos, juego de juntas, sellante de silicona, retenes, tubos de engrase, juego de juntas para turbo, taqus hidrulicos y rboles de levas.'
		}
	},
	{
		path: paths_es.productos,
		alias: paths_en.productos,
		name: 'productos',
		component: Productos,
		meta :{
			title : 'Productos',
			description: 'Ajusa ofrece una ampla gama de componentes para el motor y de piezas de recambio para vehculos. Fabricante de juntas de culata, tornillos, juego de juntas, sellante de silicona, retenes, tubos de engrase, juego de juntas para turbo, taqus hidrulicos y rboles de levas.'
		}
	},
	{
		path: paths_es.catalogo,
		alias: paths_en.catalogo,
		name: 'catalogo',
		component: Catalogo,
		meta :{
			title : 'Catlogo',
			description: 'El catlogo AJUSA es el ms completo del sector de la automocin y del recambio. AJUSA tiene dos catlogos, el catlogo general con referencias de todos los componentes del motor que fabrica y el catlogo de componentes de turbos.'
		}
	},
	{
		path: paths_es.area_tecnica, 
		alias: paths_en.area_tecnica, 
		name: 'area-tecnica',
		component: Area_tecnica,
		meta :{
			title : 'rea tcnica',
			description: 'Servicio adicional que AJUSA presta a profesionales de la automocin para mejorar el uso de sus productos. El rea tcnica ofrece un servicio de asistencia tcnica y pone a disposicin un gua de material didctico con informes tcnicos y vdeos de montaje sobre componentes del motor.'
		}
	},
	{
		path: paths_es.ajusa,
		alias: paths_en.ajusa,
		name: 'ajusa',
		component: Ajusa,
		meta :{
			title : 'Ajusa',
			description: 'AJUSA es especialista en la fabricacin de componentes para motores de combustin interna, tanto de automviles como de vehculos industriales. Lder tambin en el desarrollo de recambios para motores con turbo con su lnea de juntas para turbo y tubos de engrase.'
		}
	},
	{
		path: paths_es.actualidad,
		alias: paths_en.actualidad,
		name: 'actualidad',
		component: Actualidad,
		meta :{
			title : 'Actualidad',
			description: 'AJUSA considera que la comunicacin es una parte importante de su xito. Tiene un portal de noticias para informar a los profesionales de la automocin de todo lo que pasa en el sector del recambio y de las novedades de AJUSA.'
		}
	},
	{
		path: paths_es.contacto,
		alias: paths_en.contacto,
		name: 'contacto',
		component: Contacto,
		meta :{
			title : 'Contacto',
			description: 'Ajusa te permite contactar con la empresa y habilita un formulario en el que puedes seleccionar qu tipo de consulta quieres hacer sobre componentes del motor, sector del recambio, peticin del catlogo AJUSA, asistencia tcnica o consulta comercial.'
		}
	},
	{
		path: paths_es.blog,
		alias: paths_en.blog,
		name: 'blog',
		component: Blog,
		meta :{
			title : 'Blog',
			description: 'El blog de Ajusa es un espacio para profesionales y expertos del sector de la automocin, para mecnicos y talleres en el que te contamos las ltimas novedades de Ajusa y de todo lo que sucede en el sector del recambio.'
		}
	},
	{
		path: `${paths_es.blog_noticia}/:id`,
		alias:`${paths_en.blog_noticia}/:id`,
		name: 'ficha-blog',
		component: Ficha_blog,
		meta :{
			title : 'Blog',
			description: 'El blog de Ajusa es un espacio para profesionales y expertos del sector de la automocin, para mecnicos y talleres en el que te contamos las ltimas novedades de Ajusa y de todo lo que sucede en el sector del recambio.'
		}
	},
	{
		path: `${paths_es.blog_categoria}/:id`,
		alias:`${paths_en.blog_categoria}/:id`,
		path: '/blog/categoria/:id',
		name: 'categoria',
		component: Categoria_blog,
		meta :{
			title : 'Blog',
			description: 'El blog de Ajusa es un espacio para profesionales y expertos del sector de la automocin, para mecnicos y talleres en el que te contamos las ltimas novedades de Ajusa y de todo lo que sucede en el sector del recambio.'
		}
	},
	{
		path: paths_es.sala_prensa,
		alias: paths_en.sala_prensa,
		name: 'sala-de-prensa',
		component: SalaPrensa,
		meta :{
			title : 'Sala de prensa',
			description: 'AJUSA aprovecha los medios de comunicacin para informacin a los profesionales del sector de la automocin y del recambio de los aspectos ms destacados empresa as como de las novedades en la fabricacin de los componentes del motor.'
		}
	},
	{
		path: paths_es.trabaja_con_nosotros,
		alias: paths_en.trabaja_con_nosotros,
		name: 'trabaja-con-nosotros',
		component: TrabajaConNosotros,
		meta :{
			title : 'Trabaja con nosotros',
			description: 'En AJUSA siempre hay un espacio reservado para el talento. Puedes unirte a su equipo de trabajo rellenando el formulario que aparece en la seccin trabajo con nosotros de la web y adjunto el curriculum.'
		}
	},
	{
		path: paths_es.historia,
		alias: paths_en.historia,
		name: 'historia',
		component: Historia,
		meta :{
			title : 'Historia',
			description: 'AJUSA naci en 1972 en Albacete (Espaa). Empez fabricando juntas de culata y actualmente desarrolla una lnea completa de componentes del motor y de recambios.'
		}
	},
	{
		path: paths_es.mapa_web,
		alias: paths_en.mapa_web,
		name: 'mapa-web',
		component: MapaWeb,
		meta :{
			title : 'Ajusa',
			description: 'Ajusa'
		}
	},
	{
		path: paths_es.politica,
		alias: paths_en.politica,
		name: 'politica-de-privacidad',
		component: PoliticaPrivacidad,
		meta :{
			title : 'Poltica de privacidad',
			description: 'La Poltica de Proteccin de Datos de AUTO JUNTAS SAU descansa en el principio de responsabilidad proactiva, segn el cual el responsable del tratamiento es responsable del cumplimiento del marco normativo y jurisprudencial que gobierna dicha Poltica.'
		}
	},
	{
		path: paths_es.politica_cookies,
		alias: paths_en.politica_cookies,
		name: 'politica-de-cookies',
		component: PoliticaCookies,
		meta :{
			title : 'Poltica de cookies',
			description: 'Una cookie es un pequeo archivo de informacin que se guarda en su navegador cada vez que visita nuestra web. Guarda el historial de su actividad en las webs para que pueda identificarle y configurar su contenido en base a sus hbitos de navegacin y preferencias.'
		}
	},
	{
		path: paths_es.aviso_legal,
		alias: paths_en.aviso_legal,
		name: 'aviso-legal',
		component: AvisoLegal,
		meta :{
			title : 'Aviso legal',
			description: 'El acceso a las pginas Web de este sitio implica la aceptacin de todas las condiciones expresadas en esta informacin legal. Ajusa podr modificar la estructura y diseo del Web, as como cambiar o eliminar los contenidos y las condiciones de acceso.'
		}
	},
	{
		path: paths_es.datos_seguros,
		alias: paths_en.datos_seguros,
		name: 'tus-datos-seguros',
		component: DatosSeguros,
		meta :{
			title : 'Tus datos seguros',
			description: 'Con carcter general, sus datos personales que se faciliten a travs de la web de Ajusa sern usados para poder relacionarnos con esa persona y poder prestarle mejor servicio. Tambin pueden ser usados para enviar publicidad o promocionar nuestras actividades.'
		}
	},
	{ path: "*", redirect: '/404' },
	{ path: "/404", component: Error404 }
	],
	scrollBehavior (to, from, savedPosition) {
		if(savedPosition) {return savedPosition}else{return {x:0,y:0}}
	},
});

router.beforeEach((to, from, next) => {
	document.title = to.meta.title
	document.head.querySelector("[name=description]").content = to.meta.description;
	next()
});

export default router;




